@import '../../styles/sass/variables';
@import '../../styles/sass/mixins';

.inputForGender {
  margin-top: 12px;
  height: 45px;
  width: 120px;
  border: none;
  border-radius: 10px;
  text-align: center;
  @include text(18px, 22px, 500, normal);
  font-family: $fontFamily;
  color: rgba(181, 181, 181, 0.85);
  background: #FFFFFF;
}

.inputForGenderDark {
  margin-top: 12px;
  height: 45px;
  width: 120px;
  border: none;
  border-radius: 10px;
  text-align: center;
  @include text(18px, 22px, 500, normal);
  font-family: $fontFamily;
  color: #FFFFFF;
  background: $bgColorDark;
}

.labelNext {
  position: absolute;
  top: 240px;
  right: 60px;
  border: solid black;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
}

.labelNextDark {
  position: absolute;
  top: 240px;
  right: 60px;
  border: solid #FFFFFF;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(-45deg);
}

.labelPrev {
  position: absolute;
  top: 240px;
  right: 155px;
  border: solid black;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(135deg);
}

.labelPrevDark {
  position: absolute;
  top: 240px;
  right: 155px;
  border: solid #FFFFFF;
  border-width: 0 2px 2px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(135deg);
}